li {
  list-style:none;
}

.hidden {
  display:none;
}

.App {
  text-align: center;
  width:100%;
  max-width:768px;
  height:100vh;
  margin:0 auto;
  border:5px solid black;
}

header {
  border:1px solid blue;
  width: 100%;
  height: 60px;
  display:grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content:space-between;
}

.header-profile {
  display:grid;
  align-items: center;
}

.header-title {
  height:100%;
  line-height:60px;
  font-size:larger;
}

.header-profile .header-profile-me {
  display:grid;
  grid-template-columns: 55px auto;
}

.header-profile .header-profile-me .profileImage {
  width:55px;
  height:55px;
  border-radius:50%;
}

.header-profile .header-profile-me .profileName {
  text-align:left;
  text-indent:10px;
}

.header-nav {
  width:100%;
  height:100%;
  display:grid;
  grid-template-columns: repeat(3, 1fr);
}

.header-nav .header-nav-list {
  height: 100%;
  line-height:60px;
}

main {
  border:1px solid red;
  width:100%;
  height: calc(100% - 130px);
  max-height:880px;
  overflow:auto;
}

footer {
  border: 1px solid green;
  width:100%;
  height:70px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.footer-nav {
  height:100%;
  line-height:70px;
}

